//--|ðŸ Š Navigation.landing.scss ðŸ ˆ|--//
@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 5rem / 5rem 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
  button {
    grid-area: 1 / 1 / 2 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
  }
  h6[class*='label'] {
    margin: 0;
    display: flex;
    overflow: visible;
    font-weight: bold;
    align-items: center;
    grid-area: 1 / 2 / 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
  }

  @media screen and (max-height: 480px) {
    h6[class*='label'] {
      display: none;
    }
  }
}
@mixin mobile-portrait {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  padding: 1rem 0 0 0.5rem;
  grid-template: 5rem / 5rem 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
  button {
    grid-area: 1 / 1 / 2 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
  }
  h6[class*='label'] {
    margin: 0;
    display: flex;
    font-weight: bold;
    align-items: center;
    grid-area: 1 / 2 / 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
  }

  @media screen and (max-width: 480px) {
    padding: 0.5rem 0 0 0.25rem;
    h6[class*='label'] {
      display: none;
    }
  }
}

nav[class*='login'],
nav[class*='register'],
nav[class*='password'] {
  display: grid;
  h6[class*='login'] {
    color: Palettes.$logTicket-dark;
    filter: drop-shadow(Palettes.$logTicket-light 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
  }
  h6[class*='register'] {
    color: Palettes.$trackDay-dark;
    filter: drop-shadow(Palettes.$trackDay-light 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
  }
  h6[class*='password'] {
    color: Palettes.$findLink-dark;
    filter: drop-shadow(Palettes.$findLink-light 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
  }
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
}
