//--|ðŸ Š Form.login.scss ðŸ ˆ|--//
@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 1fr auto 1fr / 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
  div[class*='header'] {
    display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-area: 1 / 1 / 2 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    grid-template: 4rem 1fr / 4rem 1fr 4rem; //--|ðŸ Š rows / columns ðŸ ˆ|--//
    button[class*='close'] {
      background: none;
      grid-area: 1 / 3 / 2 / 4; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      img {
        width: 3rem;
        height: 3rem;
        padding: 0.5rem;
      }
    }
    div[class*='text'] {
      z-index: 1;
      display: flex;
      align-items: center;
      grid-area: 1 / 2 / 3 / 3;
      filter: drop-shadow(black 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      h4 {
        width: 100%;
        padding: 4px;
        line-clamp: 3; //--|ðŸ Š Standard property ðŸ ˆ|--//
        color: white;
        text-wrap: balance;
        text-align: center; //--|ðŸ Š Justifies text alignment ðŸ ˆ|--//
        display: -webkit-box; //--|ðŸ Š Enables multi-line truncation ðŸ ˆ|--//
        -webkit-line-clamp: 4; //--|ðŸ Š Limits the number of lines  ðŸ ˆ|--//
        text-overflow: ellipsis; //--|ðŸ Š Adds ellipsis for overflow ðŸ ˆ|--//
        -webkit-box-orient: vertical; //--|ðŸ Š Vertical Text ðŸ ˆ|--//
        filter: drop-shadow(black 0 0 1rem); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }
    }
    div[class*='icon'] {
      z-index: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: 1 / 2 / 3 / 3;
      img {
        width: 50%;
        height: 50%;
        opacity: 0.25;
        mask-size: contain;
        mask-position: center;
        mask-repeat: no-repeat;
        background: Palettes.$logTicket-light;
      }
    }
  }
  div[class*='inputs'] {
    row-gap: 1rem;
    display: inherit; //--|ðŸ ˆ display: grid; ðŸ ˆ|--//
    grid-template: repeat(2, 2rem) / 32px repeat(2, 2rem) 1fr repeat(2, 2rem) 32px; //--|ðŸ Š rows / columns ðŸ ˆ|--//

    input {
      text-align: left;
      border-radius: 4px;
      padding-left: 12px; //--|ðŸ Š padding-left: 0.75rem ðŸ ˆ|--//
      color: Palettes.$logTicket-dark;
      filter: drop-shadow(black 0 0 4px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      outline: 2px solid Palettes.$logTicket-light;
      box-shadow: inset 0 0 4px rgba(Palettes.$logTicket-dark, 1); //--|ðŸ ˆ <x-axis> | <y-axis> | <blur> | <color> ðŸ ˆ|--//

      &[id*='email'] {
        grid-area: 1 / 2 / 2 / 7; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      &[id*='password'] {
        grid-area: 2 / 2 / 3 / 7; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      &::placeholder {
        font-weight: bold;
        color: rgba(Palettes.$logTicket-dark, 0.75);
      }
    }
    button[class*='observe'] {
      grid-area: 2 / 6 / 3 / 7; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }
  div[class*='footer'] {
    display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-area: 3 / 1 / 4 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    grid-template: 1fr 6rem / repeat(2, 1fr); //--|ðŸ Š rows / columns ðŸ ˆ|--//

    menu[class*='action'] {
      display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-area: 1 / 1 / 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      grid-template: 1rem auto 1fr / 2rem 1fr 2rem; //--|ðŸ Š rows / columns ðŸ ˆ|--//

      button[class*='login'] {
        grid-area: 2 / 2 / 3 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
        filter: drop-shadow(
          rgba(Palettes.$logTicket-dark, 1) 0 0 8px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }
    }
    #login-buttons {
      display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-area: 2 / 1 / 3 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      grid-template: 1fr auto 1fr / 1fr 10rem 1fr 10rem 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
      button[class*='register'] {
        grid-area: 2 / 2 / 3 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      button[class*='password'] {
        grid-area: 2 / 4 / 3 / 5; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      button {
        filter: drop-shadow(
          rgba(Palettes.$logTicket-light, 1) 0 0 2px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }
    }
  }

  //--|ðŸ ‹ Tablet (Landscape) ðŸ ‹|--//
  @media screen and (max-height: 768px) {
  }
  @media screen and (max-height: 480px) {
    div[class*='header'] {
      display: none;
    }
    div[class*='inputs'] {
      padding-top: 3rem;
    }
  }
  @media screen and (max-height: 360px) {
    margin-top: -1rem;
    margin-bottom: -1rem;
  }
}
