form[class*='login'] {
  // display: none;
  display: grid;
  border-radius: 1rem;
  background: rgba(Palettes.$logTicket-dark, 0.75);
  box-shadow: 0 0 8rem rgba(Palettes.$logTicket-medium, 0.75); //--|ðŸ ˆ <x-axis> | <y-axis> | <blur> | <color> ðŸ ˆ|--//
  box-shadow: inset 0 0 8px rgba(Palettes.$logTicket-light, 1);
  height: 26rem;
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
